<template>
  <div id="app-statistics">
    <template v-if="statistics">
      <div class="header">
        <div class="title">
          <img class="title-thumb" :src="statistics.flag" alt />
          <div class="title-text">{{ statistics.name }}</div>
        </div>
        <div class="controls">
          <button class="btn" @click.stop="isFavourite ? onRemoveBookmark(statistics.name) : onAddBookmark(statistics.name)">
            <font-awesome-icon :class="isFavourite ? '' : 'unselected'" icon="thumbtack" />
          </button>
          <button class="btn" @click.stop="onDeleteItem(statistics.name)">
            <font-awesome-icon icon="times" />
          </button>
        </div>
      </div>
      <div class="summary">
        <div class="stats-item">
          <small class="stat-item-category">Cases</small>
          <div class="stat-item-number">{{ statistics.cases.total }}</div>
          <div>
            <small>Today: {{ statistics.cases.today }} ({{ statistics.cases.growth | toPercentage }})</small>
          </div>
        </div>
        <div class="stats-item">
          <small class="stat-item-category">Deaths</small>
          <div class="stat-item-number">{{ statistics.deaths.total }}</div>
          <div>
            <small>Today: {{ statistics.deaths.today }} ({{ statistics.deaths.growth | toPercentage }})</small>
          </div>
        </div>
      </div>
      <!-- chart -->
      <app-test-chart title="Daily Evolution" :xCategories="timelineDaily.x" :yValues1="timelineDaily.y1" :yValues2="timelineDaily.y2"></app-test-chart>
      <!-- <app-test-chart title="Cumulative Evolution" :xCategories="timelineTotal.x" :yValues1="timelineTotal.y1" :yValues2="timelineTotal.y2"></app-test-chart> -->
      <!-- <app-test-chart title="Growth (%)" :xCategories="timelineGrowth.x" :yValues1="timelineGrowth.y1" :yValues2="timelineGrowth.y2"></app-test-chart> -->
      <!-- chart -->
      <small class="last-update">Last Update: {{ lastUpdate }}</small>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import AppTestChart from "./AppTestChart";

export default {
  name: "app-statistics",

  components: {
    AppTestChart,
  },

  props: {
    statistics: {
      type: Object,
      default() {
        return [];
      },
    },
  },

  computed: {
    ...mapGetters(["storeBookmarks"]),

    isFavourite() {
      return this.storeBookmarks.includes(this.statistics.name.toLowerCase());
    },

    lastUpdate() {
      return this.statistics.cases.date;
    },

    timelineDaily() {
      const { cases, deaths } = this.statistics.timeline;
      return {
        x: cases.map((item) => item.date),
        y1: cases.map((item) => item.today),
        y2: deaths.map((item) => item.today),
      };
    },

    timelineTotal() {
      const { cases, deaths } = this.statistics.timeline;
      return {
        x: cases.map((item) => item.date),
        y1: cases.map((item) => item.total),
        y2: deaths.map((item) => item.total),
      };
    },

    timelineGrowth() {
      const { cases, deaths } = this.statistics.timeline;
      return {
        x: cases.map((item) => item.date),
        y1: cases.map((item) => item.growth),
        y2: deaths.map((item) => item.growth),
      };
    },
  },

  methods: {
    onAddBookmark(country) {
      this.$store.dispatch("addBookmark", country.trim().toLowerCase()).then(() => {
        this.$emit("notify", `${country} added to Favourites`);
      });
    },

    onRemoveBookmark(country) {
      this.$store.dispatch("removeBookmark", country.trim().toLowerCase()).then(() => {
        this.$emit("notify", `${country} removed from Favourites`);
      });
    },

    onDeleteItem(country) {
      this.$store.dispatch("removeData", country.trim().toLowerCase());
    },
  },

  filters: {
    toPercentage(value) {
      const symbol = value > 0 ? "+" : "-";
      return `${symbol}${Math.abs(value).toFixed(1)}%`;
    },
  },
};
</script>

<style lang="scss">
#app-statistics {
  padding: 1rem;
  background: rgb(173, 14, 93);
  border-radius: 12px;
  margin-bottom: 0.5rem;
  color: #fff;

  .header {
    margin-bottom: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      display: inline-flex;
      align-items: center;

      .title-text {
        margin-top: 1px;
        font-weight: bold;
        font-size: 16px;
      }

      .title-thumb {
        margin-right: 0.75rem;
        display: block;
        width: 24px;
        height: 16px;
      }
    }

    .controls {
      display: inline-flex;
      align-items: center;
    }
  }

  .summary {
    display: grid;
    grid-column-gap: 0.25rem;
    grid-template-columns: 0.5fr 0.5fr;
    margin-bottom: 0.25rem;
    font-size: 12px;

    .stats-item {
      text-align: center;
      background: lighten(#ad0e5d, 5);
      padding: 1rem 0;

      .stat-item-number {
        font-size: 26px;
        font-weight: 400;
      }
    }
  }

  .info {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: center;
    font-size: 12px;
    color: lighten(rgb(173, 14, 93), 50);
    margin-bottom: 1rem;
  }

  .btn {
    background: rgb(173, 14, 93);
    border: none;
    color: #fff;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin: 0 0.25rem;
    outline: none;

    .unselected {
      color: rgba(255, 255, 255, 0.25);
    }
  }

  .last-update {
    font-size: 10px;
    color: rgba(255, 255, 255, 0.25);
    display: block;
    margin: 1rem 0 0.25rem;
    text-align: center;
  }

  .chart {
    margin-bottom: 0.25rem;
  }
}
</style>
